<template>
  <div class="login-container">
    <div class="login-bac" :style="{backgroundImage:`url(${backgroundBac})`}"></div>
    <div class="login-content box">
      <div class="login-left" :style="{backgroundImage:`url(${bgBac})`}"></div>
      <div class="login-right box-item-flex">
        <h1>хЕиц░Сц╕╕цИП</h1>
        <div>
          <div class="login-one">
            <div class="login-icon box box-item">
              <i class="iconfont">&#xe6b0;</i>
            </div>
            <input class="login-input box-item-flex" type="text" @keyup.enter="login" v-model="userName">
          </div>
          <div class="login-one">
            <div class="login-icon box box-item">
              <i class="iconfont">&#xe602;</i>
            </div>
            <input class="login-input box-item-flex" type="password" @keyup.enter="login" v-model="passWord">
          </div>
          <el-checkbox v-model="checked">цИСхРМцДПх╣╢щБ╡хоИуАКф╗гчРЖч│╗ч╗Яф╗гчРЖхНПшооуАЛ</el-checkbox>
          <el-button type="primary" @click="login"> чЩ╗х╜Х</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import router from '@/router'
import apiConfig from '@/config/api'
import Cookie from 'js-cookie'
// import sha1 from 'sha1'
import _ from 'lodash'
export default {
  components: {

  },
  data() {
    return {
      // userName: '18975137813',
      // passWord: '111111',
      userName: '',
      passWord: '',
      checked: true,
      backgroundBac: require('../../../static/img/bg.png'),
      bgBac: require('../../../static/img/login_bac.png')
    }
  },
  created(to, from, next) {
    // 13345678902 164358  шБФхРИш┐РшРехХЖ
    // 18909876543 136123  цА╗ф╗гчРЖ
    if(process.env.NODE_ENV === 'development') {
      this.userName = '15112518221'
      this.passWord = '123456'
    }
  },
  computed: {
    userNameRight() {
      // хнЧцпНх╝Ахд┤я╝МхЕБшо╕5-16хнЧшКВя╝МхЕБшо╕хнЧцпНцХ░хнЧф╕ЛхИТч║┐
      return /^[a-zA-Z][a-zA-Z0-9_]{4,15}$/.exec(this.userName)
    },
    userPassRight() {
      // ф╗ехнЧцпНх╝Ахд┤я╝МщХ┐х║жхЬи6~18ф╣ЛщЧ┤я╝МхПкшГ╜хМЕхРлхнЧцпНуАБцХ░хнЧхТМф╕ЛхИТч║┐
      return /^[a-zA-Z]\w{5,17}$/.exec(this.passWord)
    },
    canLogin() {
      return this.userName && this.passWord && this.checked
    },
  },
  watch: {

  },
  methods: {
    login() {
      if (!this.canLogin) {
        this.$store.dispatch('toast/update', { type: 'error', content: 'шп╖ш╛УхЕехоМцХ┤ф┐бцБп', time: 2000 })
        return
      }
      let _this = this
      this.$Helper.ajax({
        method: 'GET',
        url: 'User.Login',
        params: { phone: this.userName, pwd: this.passWord },
        success: ({code,data,context}) => {
          let type = 'info'
          if (code === 1) {
            // Cookie.set('basic', data, { expires: 0.04 }) //set 1 hour's cookie 
            _this.$Helper.setCookie('basic',data)
            type = 'success'
            setTimeout(() => {
              router.push({ name: 'editAdmin' })
            }, 600)
          }
          this.$store.dispatch('toast/update', { type, content: context ? context : 'шп╖чиНхАЩ...', time: 2000 })
        },
        error: () => { },
      })
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
// @import url('/login.less');
.login-container {
  height: 100%;
  position: relative;
  .login-bac {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 0;
    // background-image: url('../../../static/img/bg.png');
    background-size: 100% 100%;
  }
  .login-content {
    position: absolute;
    background: white;
    left: 0;
    right: 0;
    top: 25vh;
    width: 43vw;
    height: 50vh;
    margin: auto;
    text-align: center;
    border-radius:2px;
    overflow: hidden;
  }
}


.login-left {
  width: 37%;
  // background-image: url('../../../static/img/login_bac.png');
  background-size: 100% 100%;
}

.login-right {
  >h1 {
    margin-top: 3.5vh;
    margin-bottom: 1.5vh;
    font-weight: 400;
  }
  >div {
    // border: 1px solid red;
    padding: 2vh 4.5vw;
  }
}

.login-one {
  // border-bottom: 1px solid black;
  position: relative; // border: 1px solid red;
  margin-top: 3vh;
}

.login-icon {
  position: absolute;
  left: 1vw;
  top: 0;
  bottom: 0;
  margin: auto;
  .iconfont {
    margin-top: .3vh;
    font-size: 1.5vw;
    color: #c8c8c8;
  }
}

.login-input {
  background: transparent;
  border: 1px solid #9E9E9E;
  outline: none;
  box-shadow: none;
  color: #444;
  font-size: 1vw; // text-align: center;
  width: 100%;
  height: 100%;
  padding: 10px 9px;
  padding-left: 5vh;
  border-radius: 3px;
}

.login-input:focus {
  border: 1px solid #20a0ff;
}

.el-checkbox {
  margin-top: 3vh;
  color: #444;
}


.el-button {
  width: 100%;
  margin-top: 5vh;
  font-size: 2vh;
}
</style>



