<template>
  <div class="container" id="container">
    <div class="h-bread" v-if="!showAction">
      {{ tableBread }}
    </div>
    <ul class="h-head box box-y-center" v-show="['achievement','complaint'].indexOf(activeSubBar)===-1">
      <li class="box-item-flex">
        <div class="box box-item">
          цА╗шобчЫИхИй
          <i class="iconfont">&#xe601;</i>
        </div>
        <div class="box">
          <div class="box-item-flex menu_one">
            <div class="box box-item">{{ basic.oneprofit }}</div>
            <div class="box box-item">ф╕кф║║</div>
            <div class="box box-item">
              <img src="../../../../../static/img/menu01.png">
            </div>
          </div>
          <div class="half-y-line"></div>
          <div class="box-item-flex menu_one">
            <div class="box box-item">{{ basic.allprofit }}</div>
            <div class="box box-item">щВАч║ж</div>
            <div class="box box-item">
              <img src="../../../../../static/img/menu01.png">
            </div>
          </div>
        </div>
      </li>
      <li v-show="basic.level !== '5'">
        <div class="box box-item">
          ф╗гчРЖш┐Фф╜г
          <i class="iconfont">&#xe601;</i>
        </div>
        <div class="box">
          <div class="box-item-flex menu_one">
            <div class="box box-item" style="color:#FF9F26;">{{ basic.commission }}</div>
            <div class="box box-item"></div>
            <div class="box box-item">
              <img src="../../../../../static/img/menu02.png">
            </div>
          </div>
        </div>
      </li>
      <li>
        <div class="box box-item">
          цА╗шобчЫ┤цОечОйхо╢
          <i class="iconfont">&#xe6b0;</i>
        </div>
        <div class="box">
          <div class="box-item-flex menu_one">
            <div class="box box-item" style="color:#44A6FF;">{{ basic.player }}</div>
            <div class="box box-item"></div>
            <div class="box box-item">
              <img src="../../../../../static/img/menu03.png">
            </div>
          </div>
        </div>
      </li>
      <li>
        <div class="box box-item">
          цЦ░хвЮ{{ addType }}цХ░щЗП
          <i class="iconfont">&#xe6b0;</i>
        </div>
        <div class="box">
          <div class="box-item-flex menu_one">
            <div class="box box-item" style="color:#80D54B;">{{ basic.new_player }}</div>
            <div class="box box-item"></div>
            <div class="box box-item">
              <img src="../../../../../static/img/menu04.png">
            </div>
          </div>
        </div>
      </li>
    </ul>
    <div class="h-table">
      <div class="box else-s-btn" v-if="['achievement','complaint'].indexOf(activeSubBar)===-1">
        <div class="box box-y-center h-table-top box-item-flex">
          <span style="color:#666;">цЯешпвцЧецЬЯя╝Ъ</span>
          <el-date-picker v-model="startDate" type="daterange" placeholder="" @change="setSeachTime" range-separator=" / "></el-date-picker>
        </div>
        <div class="box box-y-center box-x-right h-table-top box-item-flex">
          <el-input placeholder="шп╖ш╛УхЕецРЬч┤вхЖЕхо╣" icon="search" v-model="searchText" :on-icon-click="jsonSelect" @keyup.enter="jsonSelect" @change="jsonSelect">
          </el-input>
          <el-button type="primary" @click="addAgent" v-if="['editPlayer'].indexOf(activeSubBar) === -1"> + ц╖╗хКаф╗гчРЖ</el-button>
          <el-button type="primary" @click="DomainExport"> цХ░цНохп╝хЗ║</el-button>
        </div>
      </div>
      <div class="box achievement-btn" v-if="activeSubBar==='achievement'">
        <div class="box box-y-center h-table-top">
          <span style="color:#666;">цЯешпвцЧецЬЯя╝Ъ</span>
          <el-date-picker v-model="startDate" type="daterange" placeholder="" @change="setSeachTime" range-separator=" / "></el-date-picker>
        </div>
        <div class="box box-y-center h-table-top box-item-flex box-x-right">
          <span style="color:#666;">ф╗гчРЖч║зхИля╝Ъ</span>
          <el-select v-model="achievement.level" placeholder="шп╖щАЙцЛй" @change="achievementChangeFn">
            <el-option v-for="item in achievement.options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
          <el-input placeholder="шп╖ш╛УхЕецРЬч┤вхЖЕхо╣" icon="search" v-model="searchText" :on-icon-click="jsonSelect" @keyup.enter="jsonSelect" @change="jsonSelect">
          </el-input>
        </div>
      </div>
      <div class="box complaint-btn" v-if="activeSubBar==='complaint'">
        <div class="box box-y-center h-table-top box-item-flex">
          <el-button :type="complaintShowType===2?'primary':''" @click="complaintShowType=2">х╖▓хдДчРЖ</el-button>
          <el-button :type="complaintShowType===1?'primary':''" @click="complaintShowType=1"> цЬкхдДчРЖ</el-button>
        </div>
        <div class="box box-y-center h-table-top box-item-flex box-x-right">
          <span style="color:#666;">цЯешпвцЧецЬЯя╝Ъ</span>
          <el-date-picker v-model="startDate" type="daterange" placeholder="" @change="setSeachTime" range-separator=" / "></el-date-picker>
        </div>
      </div>
      <Itable :tableData="tableData" :singleCount="singleCount" :showList="showList" :tableIn="tableIn" :showAction="showAction">
      </Itable>
      <div class="viod-status box box-x-center" v-if="tableData && tableData.length === 0">
        цЪВцЧ╢цЧаф╗╗ф╜Х{{ editName }}цХ░цНоя╝Б
      </div>
      <el-pagination :current-page.sync="tableIn" :page-size="10" layout="prev, pager, next" :total="tablePageTotal">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import Itable from '@/views/components/Itable'
import _ from 'lodash'
import subBarConfig from '@/config/subbar'
import levelConfig from '@/config/level'
import Mock from 'mockjs'
import Cookie from 'js-cookie'
import apiConfig from '@/config/api'
import Vue from 'vue'
import { mapState } from 'vuex'

export default {
  components: {
    Itable,
  },
  data() {
    return {
      tableData: [],
      tableIn: 1,
      showAction: null,
      tableBread: '',
      breadLevel: null,
      breadGameId: null,
      tablePageTotal: 0,
      singleCount: 10,
      startDate: null,
      searchText: null,
      searchString: null,
      activeSubBar: null,
      achievement: {
        level: null,
        options: null,
      },
      complaintShowType: 2,
      jsonSelectSetTimer: null,
    }
  },
  created(to, from, next) {
    this.init()
  },
  computed: {
    ...mapState({
      reloadTableData: state => state.editerReloadData
    }),
    ...mapState('basic', [
      'basic',
    ]),
    ...mapState('editer', [
      'tableList',
    ]),
    showList() {
      let returnData
      if (this.breadLevel) {
        let breadLevel = levelConfig[this.breadLevel]
        returnData = subBarConfig[levelConfig[this.basic.level]].tableList[breadLevel]
      } else {
        returnData = subBarConfig[levelConfig[this.basic.level]].tableList[this.activeSubBar]
      }
      return returnData
    },
    editName() {
      return subBarConfig[this.activeSubBar].subName
    },
    addText() {
      return subBarConfig[levelConfig[this.basic.level]].addText
    },
    openLevel() {
      return this.$Helper.getLevelKey(this.activeSubBar)
    },
    addType() {
      if (this.basic.level === '2') {
        return 'цА╗ф╗гчРЖ'
      } else if (this.basic.level === '5') {
        return 'чЫ┤х▒ЮчОйхо╢'
      } else {
        return 'ф╗гчРЖ'
      }
    },
  },
  watch: {
    '$route'() {
      this.init()
    },
    reloadTableData() {
      this.init()
    },
    tableIn() {
      if (this.showAction) {
        this.initFormData()
      } else {
        this.initChildList()
      }
    },
    complaintShowType() {
      this.initFormData()
    },
    tableList() {
      this.preSetTableData(this.tableList)
    }
  },
  methods: {
    init() {
      // шо╛ч╜ощб╡щЭвч▒╗хЮЛ
      this.activeSubBar = this.$route.name
      // щЗНч╜оцРЬч┤вцХ░цНо
      this.searchText = null
      // щЗНч╜оцРЬч┤вцЧ╢щЧ┤
      this.startDate = null
      // щЗНч╜оцКХшпЙч▒╗хЮЛ
      this.complaintShowType = 2
      // щЗНч╜оф╕Ъч╗йч╗Яшобч▒╗хЮЛ
      this.achievement.level = 0
      // хКаш╜╜хИЧшбицХ░цНо
      this.initFormData()
      this.tableBread = ''
      this.breadLevel = null
      this.breadGameId = null
      this.showAction = true
      // щ╗ШшодцШ╛чд║чммф╕Ащб╡
      this.tableIn = 1
      // хИЭхзЛхМЦф╕Ъч╗йч╗ЯшобцХ░цНо
      this.initAchievementOption()

      // this.getBreadData(1,4)
    },
    initFormData({ start_time, end_time, find } = {}) {
      let userLevel = _.get(this.basic, 'level')
      let url, params
      let _this = this
      if (this.activeSubBar === 'complaint') {
        url = 'Complaint.ComplaintList'
        params = { page: this.tableIn, type: _.get(this, 'complaintShowType') }
      } else if (this.activeSubBar === 'achievement') {
        url = 'User.SpendList'
        params = { level: _.get(this, 'achievement.level') || 0, page: this.tableIn }
      } else {
        url = 'User.UserList'
        params = { level: userLevel === this.openLevel ? 0 : this.openLevel, page: this.tableIn }
      }
      if (find) {
        params.find = find
      }
      console.log(start_time,end_time)
      if (start_time && end_time) {
        params.start_time = start_time
        params.end_time = end_time
      }
      this.$Helper.ajax({
        url,
        params,
        success: (data) => {
          data.showAction = true
          _this.preSetTableData(data)
        },
        error: () => { },
      })
    },
    preSetTableData(data) {
      let breadLevel = _.get(data, 'breadLevel')
      // let levelName = breadLevel ? subBarConfig[levelConfig[breadLevel]].subName : ''
      //     levelName = levelName ? levelName : this.editName
      // let tableBread = this.tableBread + ' > ' + levelName

      this.breadLevel = breadLevel
      this.tableBread = this.getBreadData(this.openLevel, breadLevel)
      this.tableData = _.get(data, 'data.user_list') || _.get(data, 'data.complaint_list')
      this.breadGameId = _.get(data, 'breadGameId')
      this.showAction = _.get(data, 'showAction')
      this.tablePageTotal = _.get(data, 'data.page') * 10
      this.scrollTop()
    },
    getBreadData(startLevel, endlevel) {
      let s = ''
      if (startLevel && endlevel && startLevel<endlevel) {
        for(var i in levelConfig) {
          if(i >= startLevel && i <= endlevel) {
            s += '>' + subBarConfig[levelConfig[i]].subName
          }
        }
        s = s.substr(1)
      }
      return s
    },
    initAchievementOption() {
      let arr = [{ value: 0, label: 'цЙАцЬЙчнЙч║з' }]
      for (var i in levelConfig) {
        let item = {}
        if (i > this.basic.level) {
          item.value = i.toString()
          item['label'] = subBarConfig[levelConfig[i]].subName
          arr.push(item)
        }
      }
      // ф╕Ах╝АхзЛшо╛ч╜оцШ╛чд║цЙАцЬЙчФицИ╖
      let level = 0
      this.achievement = {
        options: arr,
        level,
      }
    },
    initChildList() {
      let _this = this
      let level = this.breadLevel
      let game_id = this.breadGameId
      this.$Helper.ajax({
        url: 'User.ChildrenList',
        params: {
          game_id,
          level,
          page: this.tableIn,
        },
        success: (data) => {
          data.showAction = false
          data.breadLevel = level
          data.breadGameId = game_id
          _this.$store.dispatch('editer/setTableList', data)
        },
        error: () => { },
      })
    },
    scrollTop() {
      document.getElementById('container').scrollTop = 0
    },
    setSeachTime(value) {
      if (!value) {
        this.initFormData()
        return
      }
      this.searchText = null
      let timeArr = value.split(' / ')
      let start_time = new Date(timeArr[0].replace(/-/g, '/') + ' 00:00:00').getTime() / 1000
      let end_time = (new Date(timeArr[1].replace(/-/g, '/') + ' 00:00:00').getTime() / 1000) + 86400
      console.log(end_time)
      this.initFormData({ start_time, end_time })
    },
    jsonSelect() {
      this.startDate = null
      clearTimeout(this.jsonSelectSetTimer)
      this.jsonSelectSetTimer = setTimeout(() => {
        this.initFormData({ find: this.searchText })
      }, 500);
    },
    addAgent() {
      this.$store.dispatch('modelbox/show', 'addAgent')
    },
    DomainExport() {
      this.$store.dispatch('modelbox/show', { status: 'DomainExport', params: { page: this.tablePageTotal / 10 } })
      // window.open(`http://wjpdd.yoflying.com/v2/?service=User.DomainExport&list_type=1&level=${this.openLevel}&page_start=1&page_end=1&session_id=${_.get(this.basic, 'session_id')}`)
    },
    achievementChangeFn() {
      this.startDate = null
      this.searchText = null
      this.initFormData()
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.container {
  padding: 15px;
  overflow-x: hidden;
  overflow-y: auto;
  .h-head {
    // border:1px solid red;
    height: 180px;
    width: 100%;
    li {
      height: 100%;
      width: 17.5vw;
      background: white;
      margin-left: 10px;
      div:nth-child(1) {
        height: 40%;
        font-size: 16px;
        .iconfont {
          color: #F6A623;
          font-size: 14px;
          margin-left: 2px;
        }
      }
      div:nth-child(2) {
        // border:1px solid red;
        height: 60%;
        .box-item-flex {
          height: 100%;
          text-align: center;
        }
        .menu_one {
          margin: 0;
          padding: 0;
          div:nth-child(1) {
            font-size: 30px;
            height: 40%;
            color: #FF5573;
          }
          div:nth-child(2) {
            font-size: 12px;
            color: #4A4A4A;
            height: 20%;
          }
          div:nth-child(3) {
            height: 40%;
          }
        }
        .half-y-line {
          height: 80%;
          margin-top: 3%;
          background: #666;
          border: none;
        }
      }
    }
    li:nth-child(1) {
      margin: 0;
    }
  }
  .h-table {
    margin-top: 15px; // border: 1px solid red;
    background: white;
    padding: 20px 30px;
    padding-bottom: 70px;
  }
}

.logo-name {
  font-size: 22vh;
  font-weight: bold;
  color: #e6e6e6;
}

.el-pagination {
  float: right;
  margin: 20px 10px;
}

.h-table-top {
  height: 50px;
  margin-bottom: 20px;
}

.else-s-btn {
  .el-date-editor.el-input {
    width: 210px;
  }

  .el-input {
    width: 260px;
    margin-right: 20px;
  }
}

.h-media-line {
  border-top: 1px solid #b7b7b7;
  width: 20px;
  margin-right: 15px;
}

.viod-status {
  height: 400px;
  border: 1px solid #d7d7d7;
  border-top: none;
  padding-top: 120px;
  color: #9D9D9D;
}

.achievement-btn,
.complaint-btn {
  .el-button--primary:hover {
    background: #4db3ff!important;
    color: white!important;
    border-color: #4db3ff!important;
  }
  .el-button:hover {
    border-color: #bfcbd9;
    color: #414d5b;
  }
  .el-button {
    width: 90px;
  }
}

.achievement-btn>div:nth-child(1) {
  width: 40%;
}

.achievement-btn>div:nth-child(2) {
  .el-select {
    width: 120px;
    margin-right: 30px;
  }
  .el-input {
    width: 250px;
    margin-right: 0;
  }
}

.h-bread {
  height: 30px;
  font-size: 14px;
  margin-bottom: 7px;
}
</style>



