const namespaced = true

const state = {
  basic: {
    img: "",      //хд┤хГП
    name: "",    //цШ╡чз░
    commission: "",     //ф╗гчРЖш┐Фф╜г
    oneprofit: "",    //ф╕кф║║чЫИхИй
    allprofit: "",    //щВАч║жчЫИхИй
    level: "",   //х╜УхЙНчОйхо╢чнЙч║з
    player: "",  //цА╗шобчЫ┤цОечОйхо╢
    new_player: ""   //цЦ░хвЮчОйхо╢
  }
}

const mutations = {
  SETLOGINDATA(state, data) {
    Object.assign(state.basic, data)
  },
  UPDATEIMAGEHEAD(state, data) {
    state.basic.img = data
  },
}

const actions = {
  setLoginData({ commit }, data) {
    commit('SETLOGINDATA', data)
  },
  updateImageHead({ commit }, data) {
    commit('UPDATEIMAGEHEAD', data)
  },
}

export default { namespaced, state, mutations, actions }



