<template>
  <div class="container">
    <div class="container-head box box-item">
      хЕиц░Сц╕╕цИП
    </div>
    <div class="h_menu-contaner">
      <div class="h_menu-headimg">
        <div class="head-img" @click="uploadImage" :style="{backgroundImage: `url(${basic.img ? basic.img : '../../../../static/img/default_headimg.png' })`}">
          <img src="../../../../static/img/editHeadImage.png">
        </div>
        {{basic.name}}
        <!--<img src="../../../../static/img/WechatIMG1.jpeg">-->
      </div>
      <ul class="h_memu-ul">
        <li class="h_memu-li" v-for="item in subData" :class="{ 'active':item.handle === activeItem }" v-if="authority && authority.indexOf(item.handle) !== -1 && item.handle !== 'complaintDetail'">
          <div class="h_item-div box box-y-center" @click="choseItem(item)">
            <!--<i class="iconfont" v-html="item.icon"></i>-->
            <img :src="item.icon" data-type="0">
            <img :src="item.iconActive" data-type="1"> {{ item.content }}
          </div>
        </li>
      </ul>
    </div>
    <div class="h_menu-bottom">
      <div class="box box-item" v-html="systemDate"></div>
      <div class="box box-item" v-html="systemTime"></div>
    </div>
  </div>
</template>

<script>
// import Vue from 'Vue'
import { mapState } from 'vuex'
// import authorityConfig from '@/config/authority'
import router from '@/router'
import subBarConfig from '@/config/subbar'
import levelConfig from '@/config/level'
import Cookie from 'js-cookie'
import _ from 'lodash'
export default {
  components: {

  },
  data() {
    return {
      subData: [],
      activeItem: null,
      systemDate: null,
      systemTime: null,
    }
  },
  created(to, from, next) {
    this.getTime()
    this.judgeAuthority()
    this.initSubData()
  },
  computed: {
    ...mapState('basic', [
      'basic'
    ]),
    authority() {
      let level = _.get(this.basic, 'level')
      return subBarConfig[levelConfig[level]].authority || null
    },
  },
  watch: {
    '$route'() {
      this.judgeAuthority()
    },
  },
  methods: {
    initSubData() {
      let returnData = []
      for (let i in subBarConfig) {
        let item = {}
        item.handle = i
        item.content = subBarConfig[i].subName
        item.icon = `static/img/${i}.png`
        item.iconActive = `static/img/${i}_active.png`
        returnData.push(item)
      }
      this.subData = returnData
    },
    judgeAuthority() {
      if (!this.authority) {
        router.push({name: 'Login'})
        return
      }
      if (this.authority.indexOf(this.$route.name) === -1) {
        router.push({ name: this.authority[0] })
        this.activeItem = this.authority[0]
      } else {
        this.activeItem = this.$route.name
      }
    },
    choseItem(item) {
      if( item.handle === this.$route.name ) {
        this.$Route.go(0)
        return
      }
      this.$Route.push({ name: item.handle })
      this.activeItem = item.handle
    },
    getTime() {
      this.systemDate = this.getSystemDate()
      this.systemTime = this.getSystemTime()
      setInterval(() => {
        this.systemDate = this.getSystemDate()
        this.systemTime = this.getSystemTime()
      }, 1000 * 60)
    },
    getSystemDate() {
      let date = new Date()
      return `${date.getFullYear()}х╣┤${date.getMonth() + 1}цЬИ${date.getDate()}цЧе`
    },
    getSystemTime() {
      let date = new Date()
      let week = date.getDay()
      let weekDay
      switch (week) {
        case 0:
          weekDay = 'цЧе'
          break
        case 1:
          weekDay = 'ф╕А'
          break
        case 2:
          weekDay = 'ф║М'
          break
        case 3:
          weekDay = 'ф╕Й'
          break
        case 4:
          weekDay = 'хЫЫ'
          break
        case 5:
          weekDay = 'ф║Ф'
          break
        case 6:
          weekDay = 'хЕн'
          break
        default:
          weekDay = ''
          break
      }
      let hour = date.getHours()
      hour = hour < 10 ? '0' + hour : hour
      let minutes = date.getMinutes()
      minutes = minutes < 10 ? '0' + minutes : minutes
      return `цШЯцЬЯ${weekDay} &nbsp;&nbsp;${hour} : ${minutes}`
    },
    uploadImage() {
      this.$store.dispatch('modelbox/show', 'uploadImage')
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.container {
  width: 12%;
  min-width: 220px;
  background: #222222;
  color: #666666;
  position: relative;
}

.container-head {
  font-size: 22px;
  font-weight: normal;
  color: white;
  background: #141414;
  height: 60px;
}

.h_menu-contaner {
  // border:1px solid red;
}

.h_menu-contaner+.h_menu-contaner {
  border-top: 2px solid #131e26;
}

.h_menu-bottom {
  height: 80px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  div {
    margin-top: 10px;
    font-size: 12px;
  }
}

.h_memu-head {
  padding: 10px;
  padding-left: 16px;
}

.h_menu-headimg {
  // height:100px;
  text-align: center;
  padding-bottom: 30px;
  color: #b2b2b2;
  font-size: 16px;
}

.head-img {
  height: 70px;
  border:1px solid #333;
  width: 70px;
  margin: 20px auto;
  border-radius: 100%; // border:2px solid red;
  background-size: 100%;
  background-position: center;
  position: relative;
  cursor: pointer;
  img {
    position: absolute;
    bottom: 5px;
    right: 5px;
    width: 18px;
    display: none;
  }
}

.head-img:hover {
  img {
    display: block
  }
}

.h_memu-ul {}

.h_memu-li {
  cursor: pointer;
  font-size: 14px; // border-left: 3px solid transparent;
  img {
    height: 16px;
    margin-right: 7px;
  }
  img[data-type="0"] {
    display: block;
  }
  img[data-type="1"] {
    display: none;
  }
}

.h_memu-li.active {
  background: #161616;
  color: #498ddd; // border-left: 3px solid #19a9d5;
  img[data-type="0"] {
    display: none;
  }
  img[data-type="1"] {
    display: block;
  }
}

.h_memu-li:hover {
  background: #161616;
}

.h_memu-ul i {
  margin-right: 6px;
}

.h_item-div {
  padding: 12px;
  padding-left: 26%;
  font-size: 14px;
}

.h_item-div:hover {
  padding: 12px;
  padding-left: 26%;
  font-size: 14px;
}
</style>



