<template>
  <div class="h-table-container">
    <ul class="h-table_ul h-table-head box">
      <li class="box box-item box-item-flex">х║ПхП╖</li>
      <li class="box box-item box-item-flex" v-for="(item, key0) of showList" :key="key0" :data-type="item">{{ getTableName(item) }}</li>
      <li class="box box-item box-item-flex" v-if="hideActionArr.indexOf(activeSubBar)===-1 && showAction" style="width:50px;">цУНф╜Ь</li>
    </ul>
    <ul v-for="(item,key) of tableData" :key="key" class="h-table_ul h-table-td box" :class="{cursor:activeSubBar === 'complaint'}" @click="openComplainDetail(item)">
      <li class="box box-item box-item-flex">{{ key + 1 }}</li>
      <li class="box box-item box-item-flex" v-for="(single, key2) of showList" :key="key2" :data-type="single" :class="{cursor:hasChildrenList(single,item)}" @click="showChildrenList(single,item)">{{ getTableValue(item, single) }}</li>
      <li class="box-item-flex" v-if="hideActionArr.indexOf(activeSubBar)===-1 && showAction" style="width:50px;position:relative;">
        <ISelect :id="item['game_id']" class="iselect" v-if="item.operation" :level="item['level']"></ISelect>
      </li>
      <li class="box box-item box-item-flex complaint" v-if="activeSubBar==='complaint'" style="width:50px;">
        <div class="h-default-btn" data-type="premary" @click.stop="deal('handle',item)" v-if="item.type === '1' || item.type === '2'">хдДчРЖ</div>
        <div class="h-default-btn" v-else>х╖▓хдДчРЖ</div>
        <div class="h-default-btn" data-type="premary" @click.stop="deal('conferred',item)" v-if="item.type !== '2'">х░БхП╖</div>
        <div class="h-default-btn" v-else>х╖▓х░БхП╖</div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import table from '@/config/table'
import subBarConfig from '@/config/subbar'
import levelConfig from '@/config/level'
import ISelect from './ISelect'
import router from '@/router'
import _ from 'lodash'
export default {
  components: {
    ISelect,
  },
  data() {
    return {
      activeSubBar: null,
    }
  },
  props: [
    'tableData', 'tableIn', 'singleCount', 'showList', 'tableIn', 'showAction'
  ],
  created(to, from, next) {
    // шо╛ч╜ощб╡щЭвч▒╗хЮЛ
    this.activeSubBar = this.$route.name
  },
  computed: {
    ...mapState('basic', [
      'basic',
    ]),
    openLevel() {
      return this.$Helper.getLevelKey(this.$route.name)
    },
    hideActionArr() {
      return subBarConfig[levelConfig[this.basic.level]].hideActionArr
    },
  },
  watch: {
    '$route'() {
      // шо╛ч╜ощб╡щЭвч▒╗хЮЛ
      this.activeSubBar = this.$route.name
    },
  },
  methods: {
    getTableName(name) {
      let returnName = table[name]
      return returnName
    },
    deal(type, item) {
      this.$store.dispatch('modelbox/show', { status: type, params: { game_id: item.id } })
    },
    handled(item) {
      let handled = item.handled
      if (item.conferred) { handled = 1 }
      return !handled
    },
    getTableValue(item, single) {
      let returnData = item[single] === undefined || item[single] === '' ? '-' : item[single]
      if (single === 'create_time') {
        returnData = returnData.substr(0, 10)
      } else if (single === 'content') {
        returnData = returnData.substr(0, 25) + '...'
      } else if (single === 'level' || single === 'c_level') {
        returnData = item[single] ? subBarConfig[levelConfig[item[single]]].subName : ''
      }
      return returnData
    },
    openComplainDetail(item) {
      if (this.activeSubBar === 'complaint') {
        router.push({ name: 'complaintDetail', params: { id: item.id, params: item } })
      }
    },
    hasChildrenList(single, item) {
      return ['agent_3', 'agent_4', 'agent_5', 'agent_6'].indexOf(single) !== -1 && item[single] != 0
    },
    showChildrenList(single, item) {
      if (this.hasChildrenList(single, item)) {
        let level = ['agent_3', 'agent_4', 'agent_5', 'agent_6'].indexOf(single) !== -1 ? single.split('').reverse()[0] : null
        let { game_id } = item
        let _this = this
        this.$Helper.ajax({
          url: 'User.ChildrenList',
          params: {
            game_id,
            level,
            page: 1,
          },
          success: (data) => {
            data.showAction = false
            data.breadLevel = level
            data.breadGameId = game_id
            _this.$store.dispatch('editer/setTableList', data)
          },
          error: () => { },
        })
      }
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.h-table-container {
  border: 1px solid #f3f3f3;
}

.h-table_ul {
  li {
    // border: 1px solid black;
    height: 70px;
    color: #555;
  }
}

.h-table-head {
  background: #e8f2ff;
  font-size: 16px;
  color: #424242;
}

.h-table-td {
  font-size: 14px;
  background: #ffffff;
  li {
    height: 80px;
    font-weight: 300;
    color: #000;
    text-align: center;
  }
}

.cursor {
  cursor: pointer;
}

.h-table-td:nth-child(odd) {
  background: #f6f6f6;
}

.h-table-td.even {
  background: #f6f6f6; // color:#949494;
}

.iconfont {
  font-size: 18px;
  font-weight: 600;
  color: #C0C0C0;
}

.iconfont:hover {
  color: #529efb;
}

.h-default-btn {
  border: 1px solid #c1c1c1;
  padding: 3px 0;
  width: 50px;
  text-align: center;
  font-size: 12px;
  color: #c1c1c1;
  border-radius: 2px;
  margin-right: 10px;
}

.h-default-btn[data-type="premary"] {
  border-color: #4D9CFE;
  color: #4D9CFE;
}

[data-type="content"] {
  width: 120px;
  padding: 0 20px; // overflow: hidden;
  text-align: center;
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  box-orient: vertical;
  -webkit-line-clamp: 2;
  line-clamp: 2;
}

.iselect {
  position: absolute;
  top: 25px;
  left: 0;
  right: 0;
  margin: auto;
}
</style>



