const namespaced = true

const state = {
  title: null,
  content: null,
  btn: null,
  status: false,
}

const mutations = {
  SHOW(state, data) {
    Object.assign(state, data)
  },
  CLOSE(state) {
    state.status = false
  },
}

const actions = {
  show({ commit }, { title = 'чбошодф┐бцБп', content = '', btn = {'чбошод': () => {}}, status = true }) {
    commit('SHOW', { title, content, btn, status })
  },
  close({ commit }) {
    commit('CLOSE')
  },
}

export default { namespaced, state, mutations, actions }



