
import Vue from 'vue'
import apiConfig from '@/config/api'
import levelConfig from '@/config/level'
import Router from '@/router'
import store from '@/store'
import Cookie from 'js-cookie'
const THIS = new Vue()
const { dispatch } = store
const Helper = {
  ajax({ params = {}, method = 'GET', url, success = () => { }, error = () => { } }) {
    if (!url) throw new Error('Ajax error,without url!')
    let session_id= _.get(Helper.getCookie('basic'),'session_id')
    params = session_id ? Object.assign(params,{session_id}) : params
    url = apiConfig['api'] + url
    return THIS.$http({ method, url, params }).then(
      ({ bodyText = '{}' }) => {
        let { ret, msg, data } = JSON.parse(bodyText)
        let type = 'info'
        if (ret === 406) {
          type = 'error'
          Router.push({name:'Login'})
        }
        if (msg){
          dispatch('toast/update', { type, content: msg, time: 2000 })
        }
        success(data)
      },
      () => {
        dispatch('toast/update', { type: 'error', content: 'ч╜Сч╗Ьч╣Бх┐Щя╝Мшп╖чиНхАЩхЖНшпХ', time: 2000 })
        error()
      },
    )
  },
  getCookie(key) {
    let basicData = Cookie.get(key)
    basicData = basicData && typeof basicData === 'string' ? basicData : '{}'
    return JSON.parse(basicData)
  },
  setCookie(key,data) {
    Cookie.set(key,data,{ expires: 0.04 })
    if(key === 'basic') {
      dispatch('basic/setLoginData',data)
    }
  },
  getLevelKey(name) {
    let returnData
    for(let i in levelConfig) {
      let item = levelConfig[i]
      if (item === name) {
        returnData = i
      }
    }
    return returnData
  },
}
export default Helper


