import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/pages/Login'
import Register from '@/views/pages/Register'
import Control from '@/views/pages/Control'
import Editer from '@/views/pages/Control/Mainer/Editer'
import Service from '@/views/pages/Control/Mainer/Service'
import complaintDetail from '@/views/pages/Control/Mainer/complaintDetail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
    },
    {
      path: '/control',
      component: Control,
      children: [
        {
          path: '',
          redirect: 'editer',
        },
        {
          path: 'service',
          name: 'service',
          component: Service,
        },
        {
          path: 'complaintdetail/:id',
          name: 'complaintDetail',
          component: complaintDetail,
        },
        {
          path: 'editer',
          component: Editer,
          children: [
            {
              path: '',
              redirect: 'admin',
            },
            {
              path: 'admin',
              name: 'editAdmin',
            },
            {
              path: 'operator',
              name: 'editOperator',
            },
            {
              path: 'zagent',
              name: 'editzAgent',
            },
            {
              path: 'agent',
              name: 'editAgent',
            },
            {
              path: 'promoter',
              name: 'editPromoter',
            },
            {
              path: 'player',
              name: 'editPlayer',
            },
            {
              path: 'achievement',
              name: 'achievement',
            },
            {
              path: 'complaint',
              name: 'complaint',
            },
          ],
        },
      ],
    },
  ],
})



