import vue from 'vue'
import vuex from 'vuex'
import basic from './modules/basic'
import toast from './modules/toast'
import comfirm from './modules/comfirm'
import modelbox from './modules/modelbox'
import editer from './modules/editer'
vue.use(vuex)
const store = new vuex.Store({
  state: {
    editerReloadData: false,
  },
  actions: {
    editerReload({commit}) {
      commit('EDITRELOAD')
    }
  },
  mutations: {
    EDITRELOAD(state) {
      state.editerReloadData = !state.editerReloadData
    }
  },
  getters: {},
  modules: {
    basic,
    toast,
    comfirm,
    modelbox,
    editer,
  },
})
if (module.hot) {
  module.hot.accept(
    [
      './modules/basic',
      './modules/toast',
      './modules/comfirm',
      './modules/modelbox',
      './modules/editer',
    ],
    () => store.hotUpdate({
      modules: {
        basic: require('./modules/basic').default,
        toast: require('./modules/toast').default,
        comfirm: require('./modules/comfirm').default,
        modelbox: require('./modules/modelbox').default,
        editer: require('./modules/editer').default,
      },
    }))
}
export default store



