<template>
  <ul class="topbar-container box box-item">
    <li class="box box-y-center box-item-flex crumb">
      хРОхП░чФицИ╖чобчРЖ <span @click="complainFn" v-if="showComplaint">цКХшпЙ</span>
    </li>
    <li class="box box-item changePassword" @click="changePassword">
      <img src="../../../../../static/img/changepassword.png">ф┐оцФ╣хпЖчаБ
    </li>
    <li class="box box-item loginOut" @click="loginOut">
      <img src="../../../../../static/img/loginout.png">щААхЗ║
    </li>
  </ul>
</template>

<script>
import router from '@/router'
import levelConfig from '@/config/level'
import subBarConfig from '@/config/subbar'
import { mapState } from 'vuex'
export default {
  components: {
  },
  data() {
    return {
      active: false,
    }
  },
  created(to, from, next) {
  },
  computed: {
    ...mapState('basic',[
      'basic',
    ]),
    showComplaint() {
      return subBarConfig[levelConfig[this.basic.level]].showComplaint
    },
  },
  watch: {
  },
  methods: {
    changePassword() {
      this.$store.dispatch('modelbox/show', 'changePassword')
    },
    loginOut() {
      let _this = this
      this.$Helper.ajax({
        url: 'User.LoginOut',
        success:function() {
          _this.$store.dispatch('modelbox/show', 'loginOut')
        },
      })
    },
    complainFn() {
      this.$store.dispatch('modelbox/show', 'complain')
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.topbar-container {
  height: 60px;
  background: #ffffff;
  z-index: 10;
  position: relative;
  border-bottom: 1px solid #ddd;
  li {
    height: 100%;
    img {
      width: 15px;
      margin-right: 10px;
    }
  }
  .crumb {
    font-size: 18px;
    color: #666666;
    padding-left: 25px;
    span{
      font-size:14px;
      color:#20a0ff;
      margin-left:10px;
      cursor:pointer;
    }
  }
  .changePassword,
  .loginOut {
    width: 130px;
    border-left: 1px solid #ddd;
    color: #7f7f7f;
    cursor: pointer;
  }
}

.h_bar-slide {
  position: absolute;
  top: 0;
  right: 10%;
  margin-right: 10%;
  font-size: 16px;
  color: #dfe4ed;
  ;
  cursor: pointer;
  transition: all .2s linear;
}

.h_bar-slide.active {
  background: #17232c;
}

.h_bar-slide.active .h_bar-ul {
  max-height: 1000px;
}

.h_bar-div {
  height: 40px;
  padding: 0 15px;
}

.h_bar-slide .h_bar-ul {
  max-height: 0;
  overflow: hidden;
}

.h_bar-ul li {
  padding: 10px 0;
  font-size: 12px;
  color: white;
}
</style>



