},
  // чВ╣хЗ╗ц│вч║╣цХИцЮЬ
  effectRipple = function (e, arg_opts) {
    let opts = Object.assign({
      ele: e.target, // ц│вч║╣ф╜ЬчФихЕГч┤а
      type: 'hit', // hitчВ╣хЗ╗ф╜Нч╜оцЙйцХгуААcenterф╕нх┐ГчВ╣цЙйх▒Х
      bgc: 'rgba(0, 0, 0, 0.15)' // ц│вч║╣щвЬшЙ▓
    }, arg_opts),
      target = opts.ele;
    if (target) {
      let rect = target.getBoundingClientRect(),
        ripple = target.querySelector('.e-ripple');
      if (!ripple) {
        ripple = document.createElement('span');
        ripple.className = 'e-ripple';
        ripple.style.height = ripple.style.width = Math.max(rect.width, rect.height) + 'px';
        target.appendChild(ripple);
      } else {
        ripple.className = 'e-ripple';
      }
      switch (opts.type) {
        case 'center':
          ripple.style.top = (rect.height / 2 - ripple.offsetHeight / 2) + 'px';
          ripple.style.left = (rect.width / 2 - ripple.offsetWidth / 2) + 'px';
          break;
        default:
          ripple.style.top = (e.pageY - rect.top - ripple.offsetHeight / 2 - document.body.scrollTop) + 'px';
          ripple.style.left = (e.pageX - rect.left - ripple.offsetWidth / 2 - document.body.scrollLeft) + 'px';
      }
      ripple.style.backgroundColor = opts.bgc;
      ripple.className = 'e-ripple z-active';
      return false;
    }
  },
  // database64цЦЗф╗╢ца╝х╝Пш╜мцНвф╕║2ш┐ЫхИ╢
  data2blob = function (data, mime) {
    // dataURL чЪДца╝х╝Пф╕║ тАЬdata:image/png;base64,****тАЭ,щАЧхП╖ф╣ЛхЙНщГ╜цШпф╕Аф║Ышп┤цШОцАзчЪДцЦЗхнЧя╝МцИСф╗мхПкщЬАшжБщАЧхП╖ф╣ЛхРОчЪДх░▒шбМф║Ж
    data = data.split(',')[1];
    data = window.atob(data);
    var ia = new Uint8Array(data.length);
    for (var i = 0; i < data.length; i++) {
      ia[i] = data.charCodeAt(i);
    };
    // canvas.toDataURL ш┐ФхЫЮчЪДщ╗Шшодца╝х╝Пх░▒цШп image/png
    return new Blob([ia], {
      type: mime
    });
  };

