          loading: 'цнгхЬиф╕Кф╝атАжтАж',
          noSupported: 'ц╡ПшзИхЩиф╕НцФпцМБшпехКЯшГ╜я╝Мшп╖ф╜┐чФиIE10ф╗еф╕КцИЦхЕ╢ф╗ЦчО░хЬиц╡ПшзИхЩия╝Б',
          success: 'ф╕Кф╝ацИРхКЯ',
          fail: 'хЫ╛чЙЗф╕Кф╝ахд▒ш┤е',
