          }
        },
        ru: {
          hint: '╨Э╨░╨╢╨╝╨╕╤В╨╡, ╨╕╨╗╨╕ ╨┐╨╡╤А╨╡╤В╨░╤Й╨╕╤В╨╡ ╤Д╨░╨╣╨╗ ╨▓ ╤Н╤В╨╛ ╨╛╨║╨╜╨╛',
