          noSupported: '╨Т╨░╤И ╨▒╤А╨░╤Г╨╖╨╡╤А ╨╜╨╡ ╨┐╨╛╨┤╨┤╨╡╤А╨╢╨╕╨▓╨░╨╡╤В╤Б╤П, ╨┐╨╛╨╢╨░╨╗╤Г╨╣╤Б╤В╨░, ╨╕╤Б╨┐╨╛╨╗╤М╨╖╤Г╨╣╤В╨╡ IE10 + ╨╕╨╗╨╕ ╨┤╤А╤Г╨│╨╕╨╡ ╨▒╤А╨░╤Г╨╖╨╡╤А╤Л',
          success: '╨Ч╨░╨│╤А╤Г╨╖╨║╨░ ╨▓╤Л╨┐╨╛╨╗╨╜╨╡╨╜╨░ ╤Г╤Б╨┐╨╡╤И╨╜╨╛',
          fail: '╨Ю╤И╨╕╨▒╨║╨░ ╨╖╨░╨│╤А╤Г╨╖╨║╨╕',
          preview: '╨Я╤А╨╡╨┤╨┐╤А╨╛╤Б╨╝╨╛╤В╤А',
          btn: {
            off: '╨Ю╤В╨╝╨╡╨╜╨╕╤В╤М',
            close: '╨Ч╨░╨║╤А╤Л╤В╤М',
            back: '╨Э╨░╨╖╨░╨┤',
            save: '╨б╨╛╤Е╤А╨░╨╜╨╕╤В╤М'
          },
          error: {
            onlyImg: '╨в╨╛╨╗╤М╨║╨╛ ╨╕╨╖╨╛╨▒╤А╨░╨╢╨╡╨╜╨╕╤П',
            outOfSize: '╨Ш╨╖╨╛╨▒╤А╨░╨╢╨╡╨╜╨╕╨╡ ╨┐╤А╨╡╨▓╤Л╤И╨░╨╡╤В ╨┐╤А╨╡╨┤╨╡╨╗╤М╨╜╤Л╨╣ ╤А╨░╨╖╨╝╨╡╤А: ',
            lowestPx: '╨Ь╨╕╨╜╨╕╨╝╨░╨╗╤М╨╜╤Л╨╣ ╤А╨░╨╖╨╝╨╡╤А ╨╕╨╖╨╛╨▒╤А╨░╨╢╨╡╨╜╨╕╤П: '
          }
        }
      },
      tempImgFormat = allowImgFormat.indexOf(imgFormat) === -1 ? 'jpg' : imgFormat,
      lang = langBag[langType] ? langBag[langType] : lang['zh'],
      mime = mimes[tempImgFormat];
    // шзДшМГхЫ╛чЙЗца╝х╝П
    that.imgFormat = tempImgFormat;

    if (langExt) {
      Object.assign(lang, langExt);
    }
    if (typeof FormData != 'function') {
      isSupported = false;
    }
    return {
      // хЫ╛чЙЗчЪДmime
      mime,

      // шпншиАхМЕ
      lang,

      // ц╡ПшзИхЩицШпхРжцФпцМБшпецОзф╗╢
      isSupported,
      // ц╡ПшзИхЩицШпхРжцФпцМБшзжх▒Пф║Лф╗╢
      isSupportTouch: document.hasOwnProperty("ontouchstart"),

      // цнещкд
      step: 1, //1щАЙцЛйцЦЗф╗╢ 2хЙкшгБ 3ф╕Кф╝а

      // ф╕Кф╝ачК╢цАБхПКш┐Ых║ж
      loading: 0, //0цЬкх╝АхзЛ 1цнгхЬи 2цИРхКЯ 3щФЩшпп
      progress: 0,

      // цШпхРжцЬЙщФЩшппхПКщФЩшппф┐бцБп
