      errorMsg: '',

      // щЬАц▒ВхЫ╛хо╜щлШцпФ
      ratio: width / height,

      // хОЯхЫ╛хЬ░хЭАуАБчФЯцИРхЫ╛чЙЗхЬ░хЭА
      sourceImg: null,
      sourceImgUrl: '',
      createImgUrl: '',

      // хОЯхЫ╛чЙЗцЛЦхКиф║Лф╗╢хИЭхзЛхА╝
      sourceImgMouseDown: {
        on: false,
        mX: 0, //щ╝ацаЗцМЙф╕ЛчЪДхЭРцаЗ
        mY: 0,
        x: 0, //scaleхОЯхЫ╛хЭРцаЗ
        y: 0
      },

      // чФЯцИРхЫ╛чЙЗщвДшзИчЪДхо╣хЩихдзх░П
      previewContainer: {
        width: 100,
        height: 100
      },

      // хОЯхЫ╛хо╣хЩихо╜щлШ
      sourceImgContainer: { // sic
        width: 240,
        height: 180
      },

      // хОЯхЫ╛х▒Хчд║х▒ЮцАз
      scale: {
        zoomAddOn: false, //цМЙщТоч╝йцФ╛ф║Лф╗╢х╝АхРп
        zoomSubOn: false, //цМЙщТоч╝йцФ╛ф║Лф╗╢х╝АхРп
        range: 1, //цЬАхдз100
        x: 0,
        y: 0,
        width: 0,
        height: 0,
        maxWidth: 0,
        maxHeight: 0,
        minWidth: 0, //цЬАхо╜
        minHeight: 0,
        naturalWidth: 0, //хОЯхо╜
        naturalHeight: 0
      }
    }
  },
  computed: {
    ...mapState('modelbox', [
      'status',
    ]),
    ...mapState('basic',[
      'basic',
    ]),
    session_id() {
      return _.get(this,'basic.session_id');
    },
    // ш┐Ых║жцЭбца╖х╝П
    progressStyle() {
      let {
                progress
            } = this;
      return {
        width: progress + '%'
      }
    },
    // хОЯхЫ╛ца╖х╝П
    sourceImgStyle() {
      let {
                    scale,
        sourceImgMasking
                } = this,
        top = scale.y + sourceImgMasking.y + 'px',
        left = scale.x + sourceImgMasking.x + 'px';
      return {
        top,
        left,
        width: scale.width + 'px',
        height: scale.height + 'px'
      }
    },
    // хОЯхЫ╛шТЩчЙИх▒ЮцАз
    sourceImgMasking() {
      let {
                width,
        height,
        ratio,
        sourceImgContainer
            } = this,
        sic = sourceImgContainer,
        sicRatio = sic.width / sic.height, // хОЯхЫ╛хо╣хЩихо╜щлШцпФ
        x = 0,
        y = 0,
        w = sic.width,
        h = sic.height,
        scale = 1;
      if (ratio < sicRatio) {
        scale = sic.height / height;
        w = sic.height * ratio;
        x = (sic.width - w) / 2;
      }
      if (ratio > sicRatio) {
        scale = sic.width / width;
        h = sic.width / ratio;
        y = (sic.height - h) / 2;
      }
      return {
        scale, // шТЩчЙИчЫ╕хп╣щЬАц▒Вхо╜щлШчЪДч╝йцФ╛
        x,
        y,
        width: w,
        height: h
      };
    },
    // хОЯхЫ╛щБоч╜йца╖х╝П
    sourceImgShadeStyle() {
      let {
                sourceImgMasking,
        sourceImgContainer
            } = this,
        sic = sourceImgContainer,
        sim = sourceImgMasking,
        w = sim.width == sic.width ? sim.width : (sic.width - sim.width) / 2,
        h = sim.height == sic.height ? sim.height : (sic.height - sim.height) / 2;
      return {
        width: w + 'px',
        height: h + 'px'
      };
    },
    previewStyle() {
      let {
                width,
        height,
        ratio,
        previewContainer
            } = this,
        pc = previewContainer,
        w = pc.width,
        h = pc.height,
        pcRatio = w / h;
      if (ratio < pcRatio) {
        w = pc.height * ratio;
      }
      if (ratio > pcRatio) {
        h = pc.width / ratio;
      }
      return {
        width: w + 'px',
        height: h + 'px'
      };
    }
  },
  watch: {
    value(newValue) {
      if (newValue && this.loading != 1) {
        this.reset();
      }
    }
  },
  methods: {
    // чВ╣хЗ╗ц│вч║╣цХИцЮЬ
    ripple(e) {
      effectRipple(e);
    },
    // хЕ│щЧнцОзф╗╢
    off() {
      let that = this;
      setTimeout(function () {
        that.$emit('input', false);
        that.$store.dispatch('modelbox/close')
        if (that.step == 3 && that.loading == 2) {
          that.setStep(1);
        }
      }, 200);
    },
    // шо╛ч╜оцнещкд
    setStep(no) {
      let that = this;
      setTimeout(function () {
        that.step = no;
      }, 200);
    },

    /* хЫ╛чЙЗщАЙцЛйхМ║хЯЯхЗ╜цХ░ч╗СхоЪ
     ---------------------------------------------------------------*/
    preventDefault(e) {
      e.preventDefault();
      return false;
    },
    handleClick(e) {
      if (this.loading !== 1) {
        if (e.target !== this.$refs.fileinput) {
          e.preventDefault();
          if (document.activeElement !== this.$refs) {
            this.$refs.fileinput.click();
          }
        }
      }
    },
    handleChange(e) {
      e.preventDefault();
      if (this.loading !== 1) {
        let files = e.target.files || e.dataTransfer.files;
        this.reset();
        if (this.checkFile(files[0])) {
          this.setSourceImg(files[0]);
        }
      }
    },
    /* ---------------------------------------------------------------*/

    // цгАц╡ЛщАЙцЛйчЪДцЦЗф╗╢цШпхРжхРИщАВ
    checkFile(file) {
      let that = this,
        {
                    lang,
          maxSize
                } = that;
      // ф╗ЕщЩРхЫ╛чЙЗ
      if (file.type.indexOf('image') === -1) {
        that.hasError = true;
        that.errorMsg = lang.error.onlyImg;
        return false;
      }

      // ш╢ЕхЗ║хдзх░П
      if (file.size / 1024 > maxSize) {
        that.hasError = true;
        that.errorMsg = lang.error.outOfSize + maxSize + 'kb';
        return false;
      }
      return true;
    },
    // щЗНч╜оцОзф╗╢
    reset() {
      let that = this;
      that.loading = 0;
      that.hasError = false;
      that.errorMsg = '';
      that.progress = 0;
    },
    // шо╛ч╜охЫ╛чЙЗц║Р
    setSourceImg(file) {
      let that = this,
        fr = new FileReader();
      fr.onload = function (e) {
        that.sourceImgUrl = fr.result;
        that.startCrop();
      }
      fr.readAsDataURL(file);
    },
    // хЙкшгБхЙНхЗЖхдЗх╖еф╜Ь
    startCrop() {
      let that = this,
        {
                    width,
          height,
          ratio,
          scale,
          sourceImgUrl,
          sourceImgMasking,
          lang
                } = that,
        sim = sourceImgMasking,
        img = new Image();
      img.src = sourceImgUrl;
      img.onload = function () {
        let nWidth = img.naturalWidth,
          nHeight = img.naturalHeight,
          nRatio = nWidth / nHeight,
          w = sim.width,
          h = sim.height,
          x = 0,
          y = 0;
        // хЫ╛чЙЗхГПч┤аф╕Нш╛╛цаЗ
        if (nWidth < width || nHeight < height) {
          that.hasError = true;
          that.errorMsg = lang.error.lowestPx + width + '*' + height;
          return false;
        }
        if (ratio > nRatio) {
          h = w / nRatio;
          y = (sim.height - h) / 2;
        }
        if (ratio < nRatio) {
          w = h * nRatio;
          x = (sim.width - w) / 2;
        }
        scale.range = 0;
        scale.x = x;
        scale.y = y;
        scale.width = w;
        scale.height = h;
        scale.minWidth = w;
        scale.minHeight = h;
        scale.maxWidth = nWidth * sim.scale;
        scale.maxHeight = nHeight * sim.scale;
        scale.naturalWidth = nWidth;
        scale.naturalHeight = nHeight;
        that.sourceImg = img;
        that.createImg();
        that.setStep(2);
      };
    },
    // щ╝ацаЗцМЙф╕ЛхЫ╛чЙЗхЗЖхдЗчз╗хКи
    imgStartMove(e) {
      e.preventDefault();
      // цФпцМБшзжцС╕ф║Лф╗╢я╝МхИЩщ╝ацаЗф║Лф╗╢цЧацХИ
      if (this.isSupportTouch && !e.targetTouches) {
        return false;
      }
      let et = e.targetTouches ? e.targetTouches[0] : e,
        {
                    sourceImgMouseDown,
          scale
                } = this,
        simd = sourceImgMouseDown;
      simd.mX = et.screenX;
      simd.mY = et.screenY;
      simd.x = scale.x;
      simd.y = scale.y;
      simd.on = true;
    },
    // щ╝ацаЗцМЙф╕ЛчК╢цАБф╕Лчз╗хКия╝МхЫ╛чЙЗчз╗хКи
    imgMove(e) {
      e.preventDefault();
      // цФпцМБшзжцС╕ф║Лф╗╢я╝МхИЩщ╝ацаЗф║Лф╗╢цЧацХИ
      if (this.isSupportTouch && !e.targetTouches) {
        return false;
      }
      let et = e.targetTouches ? e.targetTouches[0] : e,
        {
                    sourceImgMouseDown: {
                        on,
          mX,
          mY,
          x,
          y
                    },
          scale,
          sourceImgMasking
                } = this,
        sim = sourceImgMasking,
        nX = et.screenX,
        nY = et.screenY,
        dX = nX - mX,
        dY = nY - mY,
        rX = x + dX,
        rY = y + dY;
      if (!on) return;
      if (rX > 0) {
        rX = 0;
      }
      if (rY > 0) {
        rY = 0;
      }
      if (rX < sim.width - scale.width) {
        rX = sim.width - scale.width;
      }
      if (rY < sim.height - scale.height) {
        rY = sim.height - scale.height;
      }
      scale.x = rX;
      scale.y = rY;
    },
    // цМЙщТоцМЙф╕Лх╝АхзЛцФ╛хдз
    startZoomAdd(e) {
      let that = this,
        {
                    scale
                } = that;
      scale.zoomAddOn = true;

      function zoom() {
        if (scale.zoomAddOn) {
          let range = scale.range >= 100 ? 100 : ++scale.range;
          that.zoomImg(range);
          setTimeout(function () {
            zoom();
          }, 60);
        }
      }
      zoom();
    },
    // цМЙщТоцЭ╛х╝АцИЦчз╗х╝АхПЦц╢ИцФ╛хдз
    endZoomAdd(e) {
      this.scale.zoomAddOn = false;
    },
    // цМЙщТоцМЙф╕Лх╝АхзЛч╝йх░П
    startZoomSub(e) {
      let that = this,
        {
                    scale
                } = that;
      scale.zoomSubOn = true;

      function zoom() {
        if (scale.zoomSubOn) {
          let range = scale.range <= 0 ? 0 : --scale.range;
          that.zoomImg(range);
          setTimeout(function () {
            zoom();
          }, 60);
        }
      }
      zoom();
    },
    // цМЙщТоцЭ╛х╝АцИЦчз╗х╝АхПЦц╢Ич╝йх░П
    endZoomSub(e) {
      let {
                scale
            } = this;
      scale.zoomSubOn = false;
    },
    zoomChange(e) {
      this.zoomImg(e.target.value);
    },
    // ч╝йцФ╛хОЯхЫ╛
    zoomImg(newRange) {
      let that = this,
        {
                    sourceImgMasking,
          sourceImgMouseDown,
          scale
                } = this,
        {
                    maxWidth,
          maxHeight,
          minWidth,
          minHeight,
          width,
          height,
          x,
          y,
          range
                } = scale,
        sim = sourceImgMasking,
        // шТЩчЙИхо╜щлШ
        sWidth = sim.width,
        sHeight = sim.height,
        // цЦ░хо╜щлШ
        nWidth = minWidth + (maxWidth - minWidth) * newRange / 100,
        nHeight = minHeight + (maxHeight - minHeight) * newRange / 100,
        // цЦ░хЭРцаЗя╝Ица╣цНошТЩчЙИф╕нх┐ГчВ╣ч╝йцФ╛я╝Й
        nX = sWidth / 2 - (nWidth / width) * (sWidth / 2 - x),
        nY = sHeight / 2 - (nHeight / height) * (sHeight / 2 - y);

      // хИдцЦнцЦ░хЭРцаЗцШпхРжш╢Еш┐ЗшТЩчЙИщЩРхИ╢
      if (nX > 0) {
        nX = 0;
      }
      if (nY > 0) {
        nY = 0;
      }
      if (nX < sWidth - nWidth) {
        nX = sWidth - nWidth;
      }
      if (nY < sHeight - nHeight) {
        nY = sHeight - nHeight;
      }

      // ш╡ЛхА╝хдДчРЖ
      scale.x = nX;
      scale.y = nY;
      scale.width = nWidth;
      scale.height = nHeight;
      scale.range = newRange;
      setTimeout(function () {
        if (scale.range == newRange) {
          that.createImg();
        }
      }, 300);
    },
    // чФЯцИРщЬАц▒ВхЫ╛чЙЗ
    createImg(e) {
      let that = this,
        {
                    mime,
          sourceImg,
          scale: {
                        x,
            y,
            width,
            height
                    },
          sourceImgMasking: {
                        scale
                    }
                } = that,
        canvas = that.$refs.canvas,
        ctx = canvas.getContext('2d');
      if (e) {
        // хПЦц╢Ищ╝ацаЗцМЙф╕Лчз╗хКичК╢цАБ
        that.sourceImgMouseDown.on = false;
      }
      ctx.clearRect(0, 0, that.width, that.height);
      ctx.drawImage(sourceImg, x / scale, y / scale, width / scale, height / scale);
      that.createImgUrl = canvas.toDataURL(mime);
    },
    // ф╕Кф╝ахЫ╛чЙЗ
    upload() {
      let that = this,
        {
                    lang,
          imgFormat,
          mime,
          url,
          params,
          headers,
          field,
          ki,
          createImgUrl
                } = this,
        fmData = new FormData();
      fmData.append('file', data2blob(createImgUrl, mime), field + '.' + imgFormat);

      // ц╖╗хКахЕ╢ф╗ЦхПВцХ░
      if (typeof params == 'object' && params) {
        Object.keys(params).forEach((k) => {
          fmData.append(k, params[k]);
        })
      }

      // чЫСхРмш┐Ых║жхЫЮш░Г
      const uploadProgress = function (event) {
        if (event.lengthComputable) {
          that.progress = 100 * Math.round(event.loaded) / event.total;
        }
      };

      // ф╕Кф╝ацЦЗф╗╢
      that.reset();
      that.loading = 1;
      that.setStep(3);
      that.$emit('crop-success', createImgUrl, field, ki);
      UpladFile()
      var xhr;
      function createXMLHttpRequest() {
        if (window.ActiveXObject) {
          xhr = new ActiveXObject("Microsoft.XMLHTTP");
        }
        else if (window.XMLHttpRequest) {
          xhr = new XMLHttpRequest();
        }
      }
      function UpladFile() {
        // var fileObj = document.getElementById("file").files[0];
        var FileController = `${apiConfig.api}Upload.Upload&session_id=${_.get(that,'basic.session_id')}`;
        // var form = new FormData();
        // form.append("file", fileObj);
        createXMLHttpRequest();
        // xhr.upload.addEventListener("progress", uploadProgress, false); //чЫСхРмш┐Ых║ж
        xhr.onreadystatechange = handleStateChange;
        xhr.open("post", FileController, true);
        xhr.send(fmData);
      }
      function handleStateChange() {
        if (xhr.readyState == 4) {
          if (xhr.status == 200 || xhr.status == 0) {
            var result = xhr.responseText;
            var json = eval("(" + result + ")");
            var {ret,msg,data} = json
            var code = _.get(data,'code')
            var context = _.get(data,'context')
            var url = _.get(data,'data.url')
            var type = 'info'
            var {dispatch} = store
            if (ret === 406) {
              type = 'error'
              Router.push({name:'Login'})
            }
            if(code === 1) {
              that.$store.dispatch('modelbox/close')
              var basic = that.$Helper.getCookie('basic')
              var newBasic = Object.assign(basic,{img:url})
              that.$Helper.setCookie('basic',newBasic)
            }
            if (msg || context){
              dispatch('toast/update', { type, content: msg || context, time: 2000 })
            }
            that.loading = 2;
            that.$emit('crop-upload-success', json, field, ki);
          } else {
            that.loading = 3;
            that.hasError = true;
            that.errorMsg = lang.fail;
            that.$emit('crop-upload-fail', sts, field, ki);
          }
        }
      }


      // new Promise(function (resolve, reject) {
      //   let client = new XMLHttpRequest();
      //   client.open('POST', 'http://wjpdd.yoflying.com/v2/?service=Upload.Upload&session_id=149656831511111177362', true);
      //   client.onreadystatechange = function () {
      //     if (this.readyState !== 4) {
      //       return;
      //     }
      //     if (this.status === 200) {
      //       resolve(JSON.parse(this.responseText));
      //     } else {
      //       reject(this.status);
      //     }
      //   };
      //   client.upload.addEventListener("progress", uploadProgress, false); //чЫСхРмш┐Ых║ж
      //   // шо╛ч╜оheader
      //   if (typeof headers == 'object' && headers) {
      //     Object.keys(headers).forEach((k) => {
      //       client.setRequestHeader(k, headers[k]);
      //     })
      //   }
      //   client.send(fmData);
      // }).then(
      //   // ф╕Кф╝ацИРхКЯ
      //   function (resData) {
      //     if (that.value) {
      //       that.loading = 2;
      //       that.$emit('crop-upload-success', resData, field, ki);
      //     }

      //   },
      //   // ф╕Кф╝ахд▒ш┤е
      //   function (sts) {
      //     if (that.value) {
      //       that.loading = 3;
      //       that.hasError = true;
      //       that.errorMsg = lang.fail;
      //       that.$emit('crop-upload-fail', sts, field, ki);
      //     }
      //   }
      //   );
    },
    closeModel() {
      this.$store.dispatch('modelbox/close')
    }
  }
}

</script>

