<template>
  <div class="login-container">
    <div class="login-bac"></div>
    <div class="login-content">
      <div class="logo-name">B</div>
      <h3>хИЫх╗║ф╕Аф╕к хЕиц░Сц╕╕цИП цЦ░ш┤жцИ╖</h3>
      <form>
        <input type="text" placeholder="шп╖ш╛УхЕечФицИ╖хРН" @keyup.enter="register" v-model.trim="userName" :class="{error:!userNameRight}">
        <input type="password" placeholder="шп╖ш╛УхЕехпЖчаБ" @keyup.enter="register" v-model.trim="userPassword" :class="{error:!userPassRight}">
        <input type="password" placeholder="шп╖хЖНцмбш╛УхЕехпЖчаБ" v-model.trim="userRePassword" :class="{error:!((userPassword === userRePassword) && userRePassword!='')}">
        <div class="login-btn" :class="{error:!canRegister}" @click="register">
          ц│ихЖМ
        </div>
        <p>
          <router-link to="/login">х╖▓цЬЙш┤жхП╖я╝ЯчВ╣цИСчЩ╗х╜Х</router-link>
        </p>
      </form>
    </div>
  </div>
</template>

<script>
import sha1 from 'sha1'
import router from '@/router'
export default {
  components: {

  },
  data() {
    return {
      userName: '',
      userPassword: '',
      userRePassword: '',
    }
  },
  created(to, from, next) {

  },
  computed: {
    userNameRight() {
      // хнЧцпНх╝Ахд┤я╝МхЕБшо╕5-16хнЧшКВя╝МхЕБшо╕хнЧцпНцХ░хнЧф╕ЛхИТч║┐
      return /^[a-zA-Z][a-zA-Z0-9_]{4,15}$/.exec(this.userName)
    },
    userPassRight() {
      // ф╗ехнЧцпНх╝Ахд┤я╝МщХ┐х║жхЬи6~18ф╣ЛщЧ┤я╝МхПкшГ╜хМЕхРлхнЧцпНуАБцХ░хнЧхТМф╕ЛхИТч║┐
      return /^[a-zA-Z]\w{5,17}$/.exec(this.userPassword)
    },
    canRegister() {
      return this.userNameRight && this.userPassRight && this.userPassword === this.userRePassword
    },
  },
  watch: {

  },
  methods: {
    register() {
      if (!this.canRegister) {
        this.$store.dispatch('toast/update', { type: 'error', content: 'шп╖ш╛УхЕехоМцХ┤ф┐бцБп', time: 2000 })
        return
      }
      this.$http({
        method: 'POST',
        url: 'api/user/register',
        params: {
          name: this.userName,
          password: sha1(this.userPassword),
        },
      }).then(
        ({ bodyText }) => {
          let { code, msg } = JSON.parse(bodyText)
          let type = 'error'
          if (code === 1) {
            type = 'success'
            setTimeout(() => {
              router.push({name: 'HomePage'})
            }, 600)
          }
          this.$store.dispatch('toast/update', {
            type,
            content: msg,
            time: 2000,
          })
        },
        () => {
          this.$store.dispatch('toast/update', { type: 'error', content: 'ч╜Сч╗Ьч╣Бх┐Щя╝Мшп╖чиНхАЩхЖНшпХ', time: 2000 })
        },
      )
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import url('/login.less');
</style>



