<template>
  <div class="container">
    <div id="IModelBox" v-if="status && status!=='uploadImage'">
      <div class="layer" @click="closeModel"></div>
      <div id="IModelBox_container" :style="{top:modelConfig.top+'vh'}">
        <div class="box box-item" id="IModelBox_title">
          {{ modelConfig.title }}
          <i class="iconfont" @click="closeModel">&#xe600;</i>
        </div>
        <!--ф┐оцФ╣хпЖчаБцИРхКЯ-->
        <div class="IModelBox-content passwordSuccess" v-if="status === 'passwordSuccess'">
          <div class="box box-item">цБнхЦЬф╜а!</div>
          <div class="box box-item"> хпЖчаБф┐оцФ╣цИРхКЯ!</div>
        </div>
        <!--ф┐оцФ╣хпЖчаБ-->
        <div class="IModelBox-content changePassword" v-if="status === 'changePassword'">
          <div>
            хОЯхпЖчаБя╝Ъ
            <el-input type="password"  v-model="changePassword.password">
            </el-input>
          </div>
          <div>
            цЦ░хпЖчаБя╝Ъ
            <el-input type="password"  v-model="changePassword.newpassword">
            </el-input>
          </div>
          <div>
            цЦ░хпЖчаБчбошодя╝Ъ
            <el-input type="password"  v-model="changePassword.repassword">
            </el-input>
          </div>
          <div class="box box-item" style="margin-top:30px;">
            <el-button style="margin-right:30px;" @click="closeModel" size="small">хПЦц╢И</el-button>
            <el-button type="primary" size="small" @click="changePasswordFn">чбошод</el-button>
          </div>
        </div>
        <!--ц╖╗хКаф╗гчРЖ-->
        <div class="IModelBox-content addAgent" v-if="status === 'addAgent'">
          <div class="box input-two">
            <div class="box-item-flex" style="padding-right:15px;">
              ц╕╕цИПIDя╝Ъ
              <el-input v-model="addAgent.id">
              </el-input>
            </div>
            <div class="box-item-flex">
              хРНчз░я╝Ъ
              <el-input v-model="addAgent.name">
              </el-input>
            </div>
          </div>
          <div>
            хпЖчаБя╝Ъ
            <el-input v-model="addAgent.password">
            </el-input>
          </div>
          <div>
            цЙЛцЬ║хП╖я╝Ъ
            <el-input v-model="addAgent.phone">
            </el-input>
          </div>
          <div class="box input-two">
            <div class="box-item-flex" style="padding-right:15px;">
              ф╗гчРЖчнЙч║зя╝Ъ
              <el-select v-model="addAgent.level" placeholder="шп╖щАЙцЛй">
                <el-option v-for="item in addLevelOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </div>
            <div class="box-item-flex">
              хИЖцИРцпФф╛Ля╝Ъ
              <el-input type="number" min="1" max="100" placeholder="1~100" v-model="addAgent.percent">
              </el-input>
            </div>
          </div>
          <div class="box box-item" style="margin-top:30px;">
            <el-button style="margin-right:30px;" @click="closeModel" size="small">хПЦц╢И</el-button>
            <el-button type="primary" size="small" @click="addAgentFn">чбошод</el-button>
          </div>
        </div>
        <!--ф┐оцФ╣ф╗гчРЖхпЖчаБ-->
        <div class="IModelBox-content changeAgentPassword" v-if="status === 'changeAgentPassword'">
          <div class="box input-two">
            <div class="box-item-flex" style="padding-right:15px;">
              ц╕╕цИПIDя╝Ъ
              <el-input v-model="changeAgentPassword.id">
              </el-input>
            </div>
            <div class="box-item-flex">
              хРНчз░я╝Ъ
              <el-input  v-model="changeAgentPassword.name">
              </el-input>
            </div>
          </div>
          <div>
            цЦ░хпЖчаБя╝Ъ
            <el-input type="password" v-model="changeAgentPassword.password">
            </el-input>
          </div>
          <div class="box box-item" style="margin-top:30px;">
            <el-button style="margin-right:30px;" @click="closeModel" size="small">хПЦц╢И</el-button>
            <el-button type="primary" size="small" @click="changeAgentPasswordFn">чбошод</el-button>
          </div>
        </div>
        <!--хИащЩдф╗гчРЖ-->
        <div class="IModelBox-content deleteAgent" v-if="status === 'deleteAgent'">
          <div class="box box-item">чбошодшжБхИащЩдф╗гчРЖхРЧя╝Я</div>
          <div class="box box-item" style="margin-top:50px;">
            <el-button style="margin-right:30px;" @click="closeModel" size="small">хПЦц╢И</el-button>
            <el-button type="primary" size="small" @click="deleteAgentFn">чбошод</el-button>
          </div>
        </div>
        <!--цКХшпЙх╝╣цбЖ-->
        <div class="IModelBox-content complain" v-if="status === 'complain'">
          <div class="box box-item">
            <el-input type="textarea" :rows="12" placeholder="шп╖ш╛УхЕехЖЕхо╣" v-model="complain.complainText">
            </el-input>
          </div>
          <div class="box box-item" style="margin-top:30px;">
            <el-button style="margin-right:30px;" @click="closeModel" size="small">хПЦц╢И</el-button>
            <el-button type="primary" size="small" @click="complainFn">цКХшпЙ</el-button>
          </div>
        </div>
        <!--х░БхП╖чбошод-->
        <div class="IModelBox-content conferred" v-if="status === 'conferred'">
          <div class="box box-item">чбошодшжБх░БхП╖хРЧя╝Я</div>
          <div class="box box-item" style="margin-top:50px;">
            <el-button style="margin-right:30px;" type="primary" size="small" @click="handleConferred(2)">чбошод</el-button>
            <el-button @click="closeModel" size="small">хПЦц╢И</el-button>
          </div>
        </div>
        <!--хдДчРЖчбошод-->
        <div class="IModelBox-content complain" v-if="status === 'handle'">
          <div class="box box-item">
            <el-input type="textarea" :rows="12" placeholder="шп╖ш╛УхЕехЖЕхо╣" v-model="handle.handleText">
            </el-input>
          </div>
          <div class="box box-item" style="margin-top:50px;">
            <el-button style="margin-right:30px;" type="primary" size="small" @click="handleConferred(3)">чбошод</el-button>
            <el-button @click="closeModel" size="small">хПЦц╢И</el-button>
          </div>
        </div>
        <!--щААхЗ║чЩ╗х╜Х-->
        <div class="IModelBox-content conferred" v-if="status === 'loginOut'">
          <div class="box box-item">чбошодшжБщААхЗ║чЩ╗х╜ХхРЧя╝Я</div>
          <div class="box box-item" style="margin-top:50px;">
            <el-button style="margin-right:30px;" type="primary" size="small" @click="comfirmLoginOut">чбошод</el-button>
            <el-button @click="closeModel" size="small">хПЦц╢И</el-button>
          </div>
        </div>
        <!--цХ░цНохп╝хЗ║-->
        <div class="IModelBox-content DomainExport" v-if="status === 'DomainExport'">
          <div>х╜УхЙНхЕ▒ <span style="color:#94c0fc;">{{ DomainExportPageCount }}</span> щб╡</div>
          <div style="font-size:14px;">щАЙцЛйхп╝хЗ║</div>
          <div>
            чмм
            <input class="login-input box-item-flex" type="number" min= "1" :max="DomainExportPageCount" v-model="DomainExport.start">
            щб╡тАФчмм
            <input class="login-input box-item-flex" type="number" :min= "DomainExport.start" :max="DomainExportPageCount" v-model="DomainExport.end">
            щб╡
          </div>
          <div class="box box-item" style="margin-top:30px">
            <el-button @click="closeModel">хПЦц╢И</el-button>
            <el-button style="margin-right:30px;" type="primary"  @click="DomainExportFn">чбошод</el-button>
          </div>
        </div>
        <!--хНЗщЩНч║з-->
        <div class="IModelBox-content addAgent" v-if="status === 'changeAgentLevel'">
          <div class="box input-two">
            <div class="box-item-flex" style="padding-right:15px;">
              ф╗гчРЖчнЙч║зя╝Ъ
              <el-select v-model="changeAgentLevel.level" placeholder="шп╖щАЙцЛй">
                <el-option v-for="item in addLevelOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </div>
            <div class="box-item-flex">
              хИЖцИРцпФф╛Ля╝Ъ
              <el-input type="number" min="1" max="100" placeholder="1~100" v-model="changeAgentLevel.percent">
              </el-input>
            </div>
          </div>
          <div class="box box-item" style="margin-top:30px;">
            <el-button style="margin-right:30px;" type="primary" size="small" @click="changeAgentLevelFn">чбошод</el-button>
            <el-button @click="closeModel" size="small">хПЦц╢И</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Cookie from 'js-cookie'
import router from '@/router'
import store from '@/store'
import subBarConfig from '@/config/subbar'
import levelConfig from '@/config/level'
import apiConfig from '@/config/api'
export default {
  components: {
  },
  data() {
    return {
      changePassword: null,
      addAgent: null,
      changeAgentPassword: null,
      complain: null,
      DomainExport:null,
      changeAgentLevel: null,
      handle: null,
      resetData: {
        changePassword: {
          password: null,
          newpassword: null,
          repassword: null,
        },
        addAgent: {
          id: null,
          name: null,
          password: null,
          phone: null,
          level: null,
          percent: null,
        },
        changeAgentPassword: {
          id: null,
          name: null,
          password: null,
        },
        complain: {
          complainText: null
        },
        DomainExport: {
          start:1,
          end:1,
        },
        changeAgentLevel: {
          level: null,
          percent: null,
        },
        handle:{
          handleText: null,
        }
      }
      // status: 'conferred',
    }
  },
  created(to, from, next) {
    this.resetDataFn()
  },
  computed: {
    ...mapState('modelbox', [
      'status',
      'params'
    ]),
    ...mapState('basic',[
      'basic',
    ]),
    DomainExportPageCount(){
      return this.params && this.params.page ? this.params.page : 0;
    },
    openLevel() {
      return this.$Helper.getLevelKey(this.$route.name)
    },
    addLevelArr() {
      return subBarConfig[levelConfig[this.basic.level]].addLevelArr
    },
    addLevelOptions() {
      let returnData = []
      for(let key in levelConfig){
        if(this.addLevelArr.indexOf(parseInt(key)) === -1) continue
        let item = {}
        item.value = key
        item.label = subBarConfig[levelConfig[key]].subName
        returnData.push(item)
      }
      return returnData
    },
    modelConfig() {
      let top, title
      switch (this.status) {
        case 'passwordSuccess':
          top = 32
          title = 'ф┐оцФ╣хпЖчаБ'
          break
        case 'changePassword':
          top = 25
          title = 'ф┐оцФ╣хпЖчаБ'
          break
        case 'addAgent':
          top = 16
          title = 'ц╖╗хКаф╗гчРЖ'
          break
        case 'changeAgentPassword':
          top = 25
          title = 'ф┐оцФ╣ф╗гчРЖхпЖчаБ'
          break
        case 'deleteAgent':
          top = 32
          title = 'хИащЩдф╗гчРЖ'
          break
        case 'complain':
          top = 23
          title = 'цКХшпЙ'
          break
        case 'conferred':
          top = 32
          title = 'х░БхП╖чбошод'
          break
        case 'handle':
          top = 23
          title = 'хдДчРЖ'
          break
        case 'loginOut':
          top = 32
          title = 'щААхЗ║чЩ╗х╜Х'
          break
        case 'DomainExport':
          top = 32
          title = "цХ░цНохп╝хЗ║"
          break
        case 'changeAgentLevel':
          top = 32
          title = "хНЗщЩНч║з"
          break
        default:
          break
      }
      return { top, title }
    },
  },
  watch: {
  },
  methods: {
    resetDataFn() {
      for(var i in this.resetData) {
        this[i] = Object.assign({},this.resetData[i])
      }
    },
    closeModel() {
      this.resetDataFn()
      this.$store.dispatch('modelbox/close')
    },
    comfirmLoginOut() {
      this.closeModel()
      Cookie.set('basic', null)
      router.push({ name: 'Login' })
    },
    changePasswordFn() {
      if (!(this.changePassword.password && this.changePassword.newpassword)) {
        this.$store.dispatch('toast/update', { type: 'error', content: 'шп╖хблхЖЩхоМцХ┤ф┐бцБп', time: 2000 })
        return
      }
      if (this.changePassword.newpassword !== this.changePassword.repassword) {
        this.$store.dispatch('toast/update', { type: 'error', content: 'ф╕дцмбш╛УхЕечЪДхпЖчаБф╕Нф╕АшЗ┤', time: 2000 })
        return
      }
      const _this = this
      this.$Helper.ajax({
        url: 'User.UpdateUserPwd',
        params: {
          pwd: this.changePassword.password,
          new_pwd: this.changePassword.newpassword,
        },
        success({ code, context }) {
          const { dispatch } = store
          dispatch('toast/update', { type: 'info', content: context, time: 2000 })
          if (code === 1) {
            _this.$store.dispatch('modelbox/show','passwordSuccess')
            setTimeout(() => { _this.closeModel() }, 2000)
          }
        },
      })
    },
    addAgentFn() {
      let { id, name, password, phone, level, percent } = this.addAgent
      if (!(id && name && password && phone && level && percent)) {
        this.$store.dispatch('toast/update', { content: 'шп╖хблхЖЩхоМцХ┤ф┐бцБп', time: 2000 })
        return
      }
      percent = parseInt(percent)
      if (percent < 1 || percent > 100) {
        this.$store.dispatch('toast/update', { content: 'хИЖцИРцпФф╛ЛшМГхЫ┤цШп1хИ░100', time: 2000 })
        return
      }
      let _this = this
      this.$Helper.ajax({
        url: 'User.AddAgent',
        params: {
          game_id: id,
          name,
          pwd: password,
          phone,
          level,
          proportion: percent
        },
        success: ({ code, context, data }) => {
          if (code === undefined) {
            _this.$store.dispatch('toast/update', { content: 'ч╜Сч╗Ьч╣Бх┐Щя╝Мшп╖чиНхАЩхЖНшпХ', time: 2000 })
          }
          if (code === 0) {
            let error = ''
            data.map((index) => {
              let et
              switch (index) {
                case 'phone':
                  et = 'цЙЛцЬ║хП╖'
                  break;
                case 'game_id':
                  et = 'ц╕╕цИПID'
                  break;
                case 'proportion':
                  et = 'хИЖцИРцпФф╛Л'
                  break;
                case 'level':
                  et = 'чнЙч║з'
                  break;
                default:
                  et = ''
                  break;
              }
              error = error + et + ','
              error = error.substr(0, error.length - 1)
            })
            context = 'шп╖ш╛УхЕеф╕Аф╕кцнгчбочЪД' + error
          }
          if (code === 1) {
            setTimeout(() => { _this.closeModel() }, 600)
            _this.$store.dispatch('editerReload')
          }
          if (context) _this.$store.dispatch('toast/update', { content: context, time: 2000 })

        }
      })
    },
    deleteAgentFn() {
      let _this = this
      this.$Helper.ajax({
        url:'User.DeleteAgent',
        params:{
          game_id: _.get(this,'params.game_id')
        },
        success:({code,context,data}) => {
          if (code === 1) {
            setTimeout(() => { _this.closeModel() }, 600)
            _this.$store.dispatch('editerReload')
          }
          if (context) _this.$store.dispatch('toast/update', { content: context, time: 2000 })
        },
      })
    },
    changeAgentPasswordFn(){
      let { id, name, password } = this.changeAgentPassword
      if (!(id && name && password)) {
        this.$store.dispatch('toast/update', { content: 'шп╖хблхЖЩхоМцХ┤ф┐бцБп', time: 2000 })
        return
      }
      this.$Helper.ajax({
        url:'User.UpdateAgentPwd',
        params:{
          game_id: id,
          name,
          pwd:password
        },
        success:({code}) => {
          if (code === 1) {
            setTimeout(() => { _this.closeModel() }, 600)
          }
          _this.$store.dispatch('toast/update', { content: context, time: 2000 })
        },
      })
    },
    DomainExportFn() {
      let {start,end} = this.DomainExport
      if(start && end && start>0 && start <= end && end <= this.DomainExportPageCount) {
        this.closeModel()
        window.open(`${apiConfig.api}User.DomainExport&list_type=1&level=${this.basic.level === this.openLevel ? 0 : this.openLevel}&page_start=${start}&page_end=${end}&session_id=${_.get(this.basic, 'session_id')}`)
      } else{
        this.$store.dispatch('toast/update', { content: 'щб╡чаБш╛УхЕецЬЙшпп', time: 2000 })
      }
    },
    changeAgentLevelFn() {
      let {level, percent} = this.changeAgentLevel
      if (!(level && percent)) {
        this.$store.dispatch('toast/update', { content: 'шп╖хблхЖЩхоМцХ┤ф┐бцБп', time: 2000 })
        return
      }
      if (percent < 1 || percent > 100) {
        this.$store.dispatch('toast/update', { content: 'хИЖцИРцпФф╛ЛшМГхЫ┤цШп1хИ░100', time: 2000 })
        return
      }
      let _this = this
      this.$Helper.ajax({
        url:'User.UpdateAgentLevel',
        params:{
          game_id: _.get(this,'params.game_id'),
          level,
          proportion:percent,
        },
        success:({code,context}) => {
          if (code === 1) {
            _this.$store.dispatch('editerReload')
            setTimeout(() => { _this.closeModel() }, 600)
          }
          _this.$store.dispatch('toast/update', { content: context, time: 2000 })
        },
      })
    },
    complainFn() {
      let {complainText} = this.complain
      let _this = this
      this.$Helper.ajax({
        url:'Complaint.AddComplaint',
        params:{
          content: complainText,
        },
        success: ({code,context,data}) => {
          let type = 'info'
          if (code === 1) {
            type = 'success'
            setTimeout(() => { _this.closeModel() }, 600)
          }
          _this.$store.dispatch('toast/update', { type, content: context, time: 2000 })
        },
      })
    },
    handleConferred(handleType) {
      let _this = this
      let params = {
          id: _.get(this,'params.game_id'),
          type: handleType,
      }
      if(handleType === 3) {
        params.handle = this.handle.handleText
      }
      this.$Helper.ajax({
        url:'Complaint.HandleComplaint',
        params,
        success: ({code,context,data}) => {
          let type = 'info'
          if (code === 1) {
            type = 'success'
            _this.$store.dispatch('editerReload')
          }
          _this.$store.dispatch('toast/update', { type, content: context, time: 2000 })
          setTimeout(() => { _this.closeModel() }, 600)
        },
      })
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.container {}

#IModelBox {
  transition: all .1s linear;
}

.layer {
  background: rgba(0, 0, 0, .5);
  z-index: 11
}

#IModelBox_container {
  width: 350px;
  min-height: 200px;
  background: #f8f8f8;
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  top: 16vh;
  border-radius: 2px;
  overflow: hidden;
  z-index: 2000;
}

#IModelBox_title {
  height: 35px;
  background: #f0f0f0;
  font-size: 15px;
  font-weight: 700;
  color: #666;
  position: relative;
  .iconfont {
    position: absolute;
    right: 10px;
    top: 8px;
    font-size: 18px;
    cursor: pointer;
  }
}

#IModelBox_content {
  height: 50%;
  color: #444;
  font-size: 13px;
  padding: 0 30px;
  text-align: center;
}

#IModelBox_btn_container {
  height: 25%;
  color: #333;
  font-size: 14px
}

#IModelBox_btn_container li {
  margin-left: 20px;
  height: 26px;
  line-height: 24px;
  width: 60px;
  border-radius: 3px;
}

#IModelBox_btn_container li:first-child {
  margin: 0;
  color: white;
  background: #7b94a6;
}

#IModelBox_btn_container li:nth-child(2) {
  border: 1px solid #7b94a6;
  color: #7b94a6;
}

.IModelBox-content.passwordSuccess {
  height: 140px;
  padding-top: 15px;
  div {
    font-size: 16px;
    margin-top: 20px;
  }
}

.IModelBox-content.changePassword {
  padding: 20px 40px;
  font-size: 14px;
  div {
    margin-bottom: 10px;
    .el-input {
      margin-top: 10px;
    }
    .el-button {
      padding: 10px 30px;
    }
  }
}

.IModelBox-content.addAgent {
  padding: 20px 40px;
  font-size: 14px;
  div {
    margin-bottom: 10px;
    .el-input {
      margin-top: 10px;
    }
    .el-select {
      margin-top: 10px;
    }
    .el-button {
      padding: 10px 30px;
    }
  }
}

.IModelBox-content.changeAgentPassword {
  padding: 40px 40px 20px 40px;
  font-size: 14px;
  div {
    margin-bottom: 10px;
    .el-input {
      margin-top: 10px;
    }
    .el-select {
      margin-top: 10px;
    }
    .el-button {
      padding: 10px 30px;
    }
  }
}

.IModelBox-content.deleteAgent,
.IModelBox-content.conferred {
  height: 140px;
  padding-top: 15px;
  div {
    font-size: 16px;
    margin-top: 20px;
    .el-button {
      padding: 10px 30px;
    }
  }
}


.IModelBox-content.complain {
  padding: 30px 0;
  div {
    margin: 0;
    font-size: 16px;
