    }
    .el-textarea {
      width: 80%;
      margin: 0;
    }
  }
}

.IModelBox-content.DomainExport {
  padding: 20px 40px 10px 40px;
  div{
     font-size:16px;
     margin-bottom:10px;
     .el-button {
      padding: 10px 20px;
    }
  }
}

.login-input {
  background: transparent;
  border: 1px solid #9E9E9E;
  outline: none;
  box-shadow: none;
  color: #444;
  font-size: 1vw; // text-align: center;
  width:40px;
  padding:5px;
  border-radius: 3px;
  margin:0 5px;
}

.login-input:focus {
  border: 1px solid #20a0ff;
}
</style>



