<template>
  <div class="select-container">
    <div class="select-head box box-item">
      цЫ┤хдЪ
      <i class="iconfont">&#xe629;</i>
    </div>
    <ul class="select-ul" v-if="level!=6">
      <li @click="changeAgentPassword">ф┐оцФ╣хпЖчаБ</li>
      <li @click="deleteAgent">хИащЩдчФицИ╖</li>
      <li @click="changeAgentLevel">хНЗщЩНч║з</li>
    </ul>
    <ul class="select-ul" v-else>
      <li>ц╢Иш┤╣шпжцГЕ</li>
    </ul>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  components: {
  },
  data() {
    return {

    }
  },
  props: ['id','level'],
  created(to, from, next) {
  },
  computed: {
    ...mapState('basic',[
      'basic',
    ]),
  },
  watch: {
  },
  methods: {
    changeAgentPassword() {
      this.$store.dispatch('modelbox/show', {status:'changeAgentPassword',params:{game_id:this.id}})
    },
    deleteAgent() { 
      this.$store.dispatch('modelbox/show', {status:'deleteAgent',params:{game_id:this.id}})
    },
    changeAgentLevel() { 
      this.$store.dispatch('modelbox/show', {status:'changeAgentLevel',params:{game_id:this.id}})
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.select-container {
  width: 64px; // border:1px solid black;
  border-radius: 3px; // box-shadow: 0 0 1px #333;
  border: 1px solid #e9e9e9;
  background: white;
  cursor: pointer;
}

.select-head {
  height: 24px;
  color: #666;
  font-size: 12px;
  z-index: 0;
  position: relative;
  background: white;
  .iconfont {
    font-size: 10px;
    color: #999;
    margin-left: 5px;
  }
}

.container .select-ul {
  // display: none;
  max-height: 0;
  overflow: hidden;
  font-size: 9px;
  color: #666;
  z-index: 1;
  position: relative;
  background: white; // border:1px solid red;
  transition: all .2s linear;
  li {
    padding: 4px;
    z-index: 100;
    border-top: 1px solid #ddd;
    font-weight: 300;
    color:#111;
  }
  li:hover {
    background: #dbebfe;
    color: #20a0ff;
  }
}

.select-container:hover .select-ul {
  // display: block;
  max-height: 1000px;
}
</style>



