<template>
  <div class="container">
    <div class="rule-container">
      <div class="rule-head box">
        <div class="box box-item-flex box-y-center">
          <img src="../../../../../static/img/service.png">
          цКХшпЙф┐бцБпшпжцГЕ
          </div>
      </div>
      <ul class="h-table">
        <li class="box" v-for="(item,key) in complainConfig" v-if="!(notHandle && key==='handle')">
          <div class="s-head box box-item">
            {{ item }}
          </div>
          <div class="s-content box-item-flex box box-y-center">
            {{ getValue(key) }}
          </div>
        </li>
      </ul>
      <div class="button-container" v-show="notHandle">
        <el-button type="primary"  @click="deal('handle')"> хдДчРЖ</el-button>
        <el-button type="primary"  @click="deal('conferred')"> х░БхП╖</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import { mapState } from 'vuex'
import Router from '@/router'
import subBarConfig from '@/config/subbar'
import levelConfig from '@/config/level'
export default {
  components: {
  },
  data() {
    return {
      id:null,
      complainData:null,
      complainConfig:{
        c_game_id:'ц╕╕цИПID',
        c_name:'хРНчз░',
        c_level:'чнЙч║з',
        create_time:'цКХшпЙцЧ╢щЧ┤',
        content:'цКХшпЙхЖЕхо╣',
        handle: 'хдДчРЖцДПшзБ',
      },
    }
  },
  created(to, from, next) {
    let {id,params} = this.$route.params
    this.id = id
    if(!params) {
      params = this.$Helper.getCookie('complainData')
    }
    this.complainData = params
    this.$Helper.setCookie('complainData',params)
  },
  computed: {
    ...mapState({
      reloadTableData: state=> state.editerReloadData
    }),
    notHandle() {
      return this.complainData.type === '1' || this.complainData.type === '2'
    },
  },
  watch: {
    reloadTableData() {
      Router.go(-1)
    },
  },
  methods: {
    deal(type) {
      this.$store.dispatch('modelbox/show',{status:type,params:{game_id:this.id}})
    },
    getValue(key) {
      let returnData = this.complainData[key]
      if(key === 'c_level') {
        returnData = subBarConfig[levelConfig[this.complainData[key]]].subName
      }
      if(key === 'type' && returnData === '3') {
        returnData = 'х╖▓х░БхП╖'
      }
      return returnData
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.container {
  padding: 20px;
  overflow-x: hidden;
  overflow-y: auto;
}

.rule-container{
  margin-top: 5px; // border: 1px solid red;
  background: white;
  padding: 30px 40px;
  padding-bottom: 30px;
}

.rule-head {
  height: 60px;
  background: #EEF5FF;
  padding:0 20px;
  div:nth-child(1){
    img{
      height:18px;
      margin-right:5px;
    }
  }
  div:nth-child(2){
    color:#4D9CFE;
    font-size:14px;
    span{
      cursor:pointer;
    }
  }
}

.h-table{
  li:nth-child(1){
    .s-head{
      border-top:none;
    }
    .s-content{
      border-top:none;
    }
  }
  li{
    min-height:95px;
    .s-head{
      border-top:1px solid #ccc;
      width:30%;
    }
    .s-content{
      border-top:1px solid #ccc;
      border-left:1px solid #ccc;
      padding:20px 10%;
      // padding-left:10%;
    }
  }
}
.el-button{
  padding:10px 20px;
}
.button-container{
  margin-top:30px;
}
</style>



