<template>
  <div class="container">
    <transition name="fade">
      <div  id="Icomfirm" v-show="status">
        <div class="layer"></div>
        <div id="Icomfirm_container">
          <div class="box box-item" id="Icomfirm_title">{{ title }}</div>
          <div class="half-x-line" style="background:rgba(200,200,200,.9)"></div>
          <div class="box box-item" id="Icomfirm_content">{{ content }}</div>
          <ul class="box box-x-center" id="Icomfirm_btn_container">
            <li class="box box-x-center cursor" v-for="(item,index) in btn" @click="Fun(item)" :key="index">{{ index }}</li>
          </ul>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  components: {
  },
  data() {
    return {
      setTimer: null,
    }
  },
  created(to, from, next) {

  },
  computed: {
    ...mapState('comfirm', [
      'title',
      'content',
      'btn',
      'status',
    ]),
  },
  watch: {
  },
  methods: {
    test() {
      alert(0)
    },
    Fun(fun) {
      fun.call(this)
      this.$store.dispatch('comfirm/close')
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.container {
}

#Icomfirm {
  transition: all .1s linear;
}

#Icomfirm_container {
  width: 350px;
  height: 160px;
  background: #f8f8f8;
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  top: 35vh;
  border-radius: 4px;
  overflow: hidden;
  z-index: 2000;
}

#Icomfirm_title {
  height: 25%;
  background: #5f666e;
  font-size: 15px;
  font-weight: 700;
  color: white;
}

#Icomfirm_content {
  height: 50%;
  color: #444;
  font-size: 13px;
  padding: 0 30px;
  text-align: center;
}

#Icomfirm_btn_container {
  height: 25%;
  color: #333;
  font-size: 14px
}

#Icomfirm_btn_container li {
  margin-left: 20px;
  height: 26px;
  line-height:24px;
  width: 60px;
  border-radius: 3px;
}

#Icomfirm_btn_container li:first-child {
  margin: 0;
  color: white;
  background: #7b94a6;
}

#Icomfirm_btn_container li:nth-child(2) {
  border: 1px solid #7b94a6;
  color: #7b94a6;
}
</style>



